import React from 'react';

import { BookmarkIcon } from '../assets/BookmarkIcon';
import { Button } from '../src';

async function promiseClick() {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(null);
    }, 1000);
  });
}

export default {
  primary: <Button variant="primary"></Button>,
  secondary: <Button variant="secondary"></Button>,
  shadow: <Button variant="shadow"></Button>,
  'shadow-bordered': (
    <Button variant="shadow" bordered>
      
    </Button>
  ),
  loading: (
    <Button loading variant="primary">
      
    </Button>
  ),
  success: (
    <Button status="success" variant="primary">
      
    </Button>
  ),
  error: (
    <Button status="error" variant="primary">
      
    </Button>
  ),
  promiseClick: (
    <Button variant="primary" onClick={promiseClick}>
      
    </Button>
  ),
  disabled: (
    <Button variant="primary" disabled bordered={false}>
      
    </Button>
  ),
  'min-width': <Button minWidth={160}></Button>,
  block: <Button block></Button>,
  icon: (
    <Button
      variant="primary"
      bordered={false}
      disabled={false}
      icon={<BookmarkIcon />}
    />
  ),
  iconLeft: (
    <Button
      variant="primary"
      bordered={false}
      disabled={false}
      iconLeft={<BookmarkIcon />}
    >
      
    </Button>
  ),
  'min-width-with-iconLeft': (
    <Button minWidth={160} iconLeft={<BookmarkIcon />}>
      
    </Button>
  ),
  iconRight: (
    <Button
      variant="primary"
      bordered={false}
      disabled={false}
      iconRight={<BookmarkIcon />}
    >
      
    </Button>
  ),
  iconBoth: (
    <Button
      variant="primary"
      bordered={false}
      disabled={false}
      iconLeft={<BookmarkIcon />}
      iconRight={<BookmarkIcon />}
    >
      
    </Button>
  ),
};
